<template>
  <div class="chat" style="margin-bottom: 1rem">
    <div class="content-show" v-for="(item,index) in dataList" :key="index">
      <!-- Left -->
      <div class="sender">
        <div>
          <img src="../../assets/img/mine.jpg" />
        </div>
        <div>
          <div class="left_triangle"></div>
          <span>{{item.question}}</span>
        </div>
      </div>
      <!-- Right -->
      <div class="receiver">
        <div>
          <img src="../../assets/img/machine.jpg" />
        </div>
        <div>
          <div class="right_triangle"></div>
          <span>{{item.answer}}</span>
        </div>
      </div>
    </div>
    <div class="content-send">
      <input type="text" v-model="val" />
      <button @click="send">发送</button>
    </div>
  </div>
</template>
<script>
import md5 from "blueimp-md5";
import axios from "axios";
export default {
  created() {
    let obj = {
      title: "聊天",
      className: "chat"
    };
    this.$emit("changeNav", obj);
  },
  data() {
    return {
      val: "",
      dataList: []
    };
  },
  methods: {
    getTimeStamp() {
      let now = new Date();
      now = (Date.parse(now) / 1000).toString();
      return now;
    },
    getNonceStr() {
      let str = Math.random()
        .toString(36)
        .substr(2);
      return str;
    },
    getReqSign(params) {
      //1.字典排序
      let arr = Object.keys(params).sort();
      //2.对排序后的对象进行url转码
      let str = "";
      for (let i = 0; i < arr.length; i++) {
        str += arr[i] + "=" + encodeURI(params[arr[i]]) + "&";
      }
      //3.拼接appkey
      str += "app_key=Gbbo3fKX2UaBla9n";
      //4.md5加密
      str = md5(str).toUpperCase();
      return str;
    },
    send() {
      let params = {
        app_id: "2119583781",
        time_stamp: this.getTimeStamp(),
        nonce_str: this.getNonceStr(),
        session: "10000",
        question: this.val
      };
      params.sign = this.getReqSign(params);
      //发送请求
      axios
        .get(
          "https://bird.ioliu.cn/v2?url=https://api.ai.qq.com/fcgi-bin/nlp/nlp_textchat",
          {
            params
          }
        )
        .then(res => {
          this.dataList.push({
            answer: res.data.data.answer,
            question: this.val
          });
        })
        .catch(() => {
          return false;
        });
    }
  }
};
</script>
<style scoped>
.chat {
  font-size: 0.4rem;
  background: url('../../assets/img/chatImg.jpg') fixed center;
}
.content-send {
  position: fixed;
  bottom: 1rem;
  width: 100%;
  height: 1rem;
  background-color: rgb(169, 169, 235);
}
.content-send button {
  position: absolute;
  top: 50%;
  left: 78%;
  transform: translateY(-50%);
  padding: 0 6px;
  height: 30px;
  width: 14%;
  background-color: rgb(96, 236, 15);
  color: #ddd;
}
.content-send input {
  position: absolute;
  top: 50%;
  left: 8%;
  transform: translateY(-50%);
  padding: 0;
  height: 26px;
  width: 70%;
}
.content-show {
  position: relative;
  font-family: cursive;
}
/* bubble style */
.sender::after{
  content: "";
  display: block;
  clear:both;
}
.sender div:nth-of-type(1){
  float: left;
}
.sender div:nth-of-type(2){
  background-color: aquamarine;
  float: left;
  margin: 0 20px 10px 15px;
  padding: 10px 10px 10px 0px;
  border-radius:7px;
}

.receiver div:first-child img,
.sender div:first-child img{
  width:50px;
  height: 50px;
  border-radius: 100%;
}

.receiver::after{
  content: "";
  display: block;
  clear:both;
}
.receiver div:nth-child(1){
  float: right;
}
.receiver div:nth-of-type(2){
  float:right;
  background-color: gold;
  margin: 0 10px 10px 20px;
  padding: 10px 0px 10px 10px;
  border-radius:7px;
  width: 230px;
}

.left_triangle{
  height:0px;  
  width:0px;  
  border-width:8px;  
  border-style:solid;  
  border-color:transparent aquamarine transparent transparent;  
  position: relative;
  left:-16px;
  top:3px;
}

.right_triangle{
  height:0px;  
  width:0px;  
  border-width:8px;  
  border-style:solid;  
  border-color:transparent transparent transparent gold;  
  position: relative;
  right:-16px;
  top:3px;
}

</style>
